@import "colors";
nav{
    background-color: rgb(236, 207, 141);
    width: 100%;
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    top: 0%;
    z-index: 10;
    
    
>h1{
    text-transform: uppercase;
        background: linear-gradient(to right, #2b2b2c 0%, #e9b816 100%);
        
        -webkit-background-clip: text;
        //background-clip: unset;
        -webkit-text-fill-color: transparent;
        display: inline-block;
      
}
>main{
    width:70%;
    display:flex;
    justify-content:flex-end;


    >a{
    color: $dark;
    margin:1rem;

        &:hover{
        color:white;
        
        }
}

}



}