@import "./colors" ;
.contact{
background-color:$second;
width:100%;
height:100vh;
display: flex;
justify-content: center;
align-items: center;
>main{
    background-color: $first;
    height:27rem;
    width:70%;
    margin-top: -15px;
    border-radius: 0 200px 200px 0;
    >form{
        margin:5rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width:60%;
        >div{
            height:3rem;
            width:60%;
            display: flex;
            justify-content: flex-end;
            margin:0.5rem;
            align-items: center;
            >label{
                font:400 1rem $text1;
                width:20%;
                margin:0.1rem;

            }
            >input{
                align-self: stretch;
                width:80%;
                padding:1rem;
                border-radius: 5px;
                border:1px solid rgba(123,123,123,0.6);
                outline:none;
            }
            
        }
    
                     
                 
        .btn-grad {
            background-image: linear-gradient(to right, #fceabb 0%, #f8b500  51%, #fceabb  100%);
            margin: 10px;
            margin-left: 15px;
    
            padding: 15px 60px;
            text-align: center;
            text-transform: uppercase;
            transition: 0.5s;
            background-size: 200% auto;
            color: rgb(18, 16, 16);            
            box-shadow: 0 0 20px #eee;
            border-radius: 10px;
            border-color: rgb(234, 181, 60);
            display: block;
          }

          .btn-grad:hover {
            background-position: right center; /* change the direction of the change here */
            color: #fff;
            text-decoration: none;
          }
         
         
            
        }
    }
}


