@import "./colors.scss";
@media screen and (max-width: 1104px){
    .contact>main>form>div>label{
        margin:0.5rem;
    }
    .contact>main>form>div>input{
        margin-left: 2px;
    }
    .contact>main>form>div>.tag{
        margin-left: 5px;

    }

}

@media screen and (max-width: 928px){
    nav{
        width:auto;
        >main{
            margin-right: 10px;
        }
    }
    footer{
        >div>.linkstab{
            margin-right: 50px;
        }
    }

}
@media screen and (max-width: 857px){
    footer{
position:static;
bottom: 0%;

    }

}

@media screen and (max-width: 820px){
.home3{
    height:130vh;
    >div{
        height:120vh;

    }
}
.contact>main{
    height:100vh;
    width:100%;
    border-radius: 0;
    h1{
        text-align: center;
    }
}
};
@media screen and (max-width: 700px){
    nav{
        >h1{
            display:none;
        }
        >main{
            width: 100%;
            justify-content: center;
            a{
                margin:0.4rem;
            }
        }

    }
    .home>main{
        align-items: center;
        h1{
            font-size: 3rem;
        }
    }
    .home2{
        height:150vh;
        flex-direction: column;
        >img{
            width:60%;
        }
        >p{
            letter-spacing: 1px;
            margin:1rem 1rem 0 1rem;
            width:80%;
        }
    }
    .home3>div{
        text-align: center;
        width:100%;
        border-radius: 0;
        >p{
            margin-top: -50px;
            padding: 5rem 2rem;
            font-size: 0.9rem;
            line-height: 148%;


        }
    }
    .home4{
   
        >div{
            height:120vh;
            width:100%;
            border-radius: 0 0 0 0;
            margin-top: -200px;
           
            >h1{
                margin-top: -2rem;
            }
            >article{
                margin-top:1rem;
                display:flex;
                flex-direction: column;
            
                
                //padding:0 1rem 0 1rem;
            
            >div{
                width:10rem;
                margin-top: 1rem;;
                height:10rem;
                margin:0.8rem;
            }
        }
        }

    }
    .contact>main{
        padding:1rem;
        >h1{
            margin-top:10rem;

        }
        >form>div{
            width:112%;
            margin-top: 2rem;
            >label{
                width:29%;
            }
        }
        form>button{
            width:200px;
            margin-top: 1rem;
        }
    }
    .services{
        height:70vh;
    }
    
};
    
