@import "./colors";


footer{
   
    width:100%;
    background-color: black;
    height:200px;
    //color:$dark;
    display: grid;
    grid-template-columns: 2fr 1fr;
    border:0;
    //margin-top:-20px;
    //padding:2rem;
    //border: 5px;
   // border-color: grey;
    //border-style: none;
    >div{
        width:100%;
        position: absolute;
        
        >h1{
            margin:80px 0 0 100px;
            font-size: 2.5rem;
            color: rgb(222, 179, 98);
        }
        .linkstab{
            
            
            position: absolute;
            right:0px;
            top:0px;
            margin-right: 20px;
            margin-top: 15px;
            color: white;
            
            
            a{
                padding: 1px;
                width:9rem;
                height:2rem;
                color:white;
                
            
            
            &:hover{
                color:rgb(244, 175, 54);
            }
        }

        }
        .copy{
            width: 100%;
            display: flex;
            //align-items: center;
            justify-content: center;
            flex-direction: row;
            margin-top: 50px;

        >p{
            font-size: 1rem;
            color:white;
            
            
        }
    }
    }
    
    

       
    &::before{
        content: "";
        width:100%;
        height:200px;
        position: absolute;
        background:no-repeat center/auto url(../assets/footer.jpg);
        background-size:cover;
       filter:blur(5px);
       
    }

}
