@import "colors";

.home {
    background-color: $second;
    width:100%;
    height:100vh;
    border:0;
    border-radius: 0 0 200px 200px;
>main{
    position: absolute;
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height:100%;
    padding:2rem;
    border-radius: 0 0 200px 200px;

    >h1{
        font:400 6rem $text1 ;
        color:white;

    }
    >p{
        font:900 1rem $text1;
        color:rgb(244, 217, 143);
    }

}
&::before{
    content: "";
    width:100%;
    height:100%;
    position: absolute;
    background:no-repeat center/auto url(../assets/background.jpg);
    background-size: cover;
   // filter:blur(3px);

}

}
.home2{
    width:100%;
    height:100vh;
    background-color: $second;
    padding: 500px 0;
    display:flex;
    justify-content: center;
    align-items: center;
    margin-top: -200px;

>img{
    width:30%;
    animation: imgup 0.7s linear infinite alternate;
    padding-right: 2rem;

}
>p{
    color:$first;
    font:100 1rem $text2;
    width:50%;
    letter-spacing: 2px;
    word-wrap: 5px;
    text-align: justify;
    margin-right:50px;

}
>div{
    width:50%;
    display: flex;
    flex-direction: column;
    padding:5rem;
    justify-content: center;
    align-items: center;
    height:100vh;


}
}


@keyframes imgup {
    to{transform: translateY(-10px);}
}
.home3{
    width:100%;
    height:120vh;
    background-color: $second;
    display:flex;
    margin-top: -200px;

    >div{
        width:70%;
        height:100vh;
        background-color: $first;
        display:flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border-radius: 0 200px 200px 0;
        padding: 2rem;
    
    >h1{
        text-transform: uppercase;
        border-bottom: 2px solid $dark;
        padding:0.5rem;
        width:15rem;
        color:$dark;
       


    }
    >p{
        font:italic 100 1.2rem text2;
       
        padding:5rem;
        letter-spacing: 1px;
        word-spacing: 5px;
        line-height: 170%;
    }
}
}
.home4{
    width:100%;
    height:90vh;
    background-color:$second;
    display:flex;
    justify-content: flex-end;

    >div{
        background-color: rgb(249, 199, 108);
        border-radius: 200px 0 0 200px;
        width:70%;
        height:70vh;
        padding:2rem;
        display:flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: center;
        >h1{
            text-transform: uppercase;
            width:140px;
            color: $first;
            border-bottom: 2px solid $first;
            padding:0.5rem;

        }
        >article{
            display: flex;
            margin:2rem 0;
            flex-wrap: wrap;
            justify-self: center;
            >div{
                margin:1rem;
                width:10rem;
                height:10rem;
                animation: imgup 0.7s linear infinite;
                border-radius: 50%;
                background-color:$first;
                display:flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                color:$dark;
                cursor: pointer;
                transition:all 0.5s;
                &:hover{
                    filter:invert(1)
                }
                svg{
                    font-size: 4rem;
                }
                >p{
                    font:400 1.2rem $text2;
                
                }
            }
        }
    }
}
    


